                            <b-form-group id="input-group-1" label="Name:" label-for="input-1">
                                <b-form-input id="input-1"
                                              size="sm"
                                              v-model="form.name"
                                              placeholder="Enter name"
                                              required></b-form-input>
                            </b-form-group>

                            <b-form-group id="input-group-2"
                                          label="Email:"
                                          label-for="input-2">
                                <b-form-input id="input-2"
                                              size="sm"
                                              v-model="form.email"
                                              type="email"
                                              placeholder="Enter email"
                                              required></b-form-input>
                            </b-form-group>

                            <b-form-group id="input-group-3" label="Message:" label-for="input-3">
                                <b-form-textarea id="input-3"
                                                 size="sm"
                                                 v-model="form.message"
                                                 placeholder="Enter message"
                                                 rows="7"
                                                 no-resize
                                                 required></b-form-textarea>
                            </b-form-group>
                            <div class="centerAlign">
                                <b-button type="submit" variant="success" size="sm" class="btnSpace">Send</b-button>
                                <b-button type="reset" variant="danger" size="sm">Reset</b-button>
