                        </b-form>
                    </div>
                    <div class="centerAlign">
                        <div class="mediaZoom">
                            <b-avatar size="md" icon="github" href="https://github.com/j-ayala"></b-avatar>
                        </div>
                        <div class="mediaZoom">
                            <b-avatar size="md" icon="linkedin" href="http://linkedin.com/in/jason-ayala"></b-avatar>
                        </div>
                        <div class="mediaZoom">
