</template>

<script>
    export default {
        name: 'Contact',
        data() {
            return {
                form: {
                    name: '',
                    email: '',
                    message: '',
                },
                show: true
            }
        },
        methods: {
            onSubmit(event) {
                event.preventDefault()
                alert(JSON.stringify(this.form))
            },
            onReset(event) {
                event.preventDefault()
                // Reset our form values
                this.form.email = ''
                this.form.name = ''
                this.form.message = ''
                // Trick to reset/clear native browser form validation state
                this.show = false
                this.$nextTick(() => {
                    this.show = true
                })      
            }
        }
    }
</script>